import React from "react";
import { Route } from "react-router";
import { BrowserRouter} from 'react-router-dom'; 
// Picutre
import logo_Jems from './pictures/logo_Jems.png';
import Management from './pictures/Championship/management.jpg';
// Page
import Thumbnail from './Thumbnail.js';
import jemsPage from './jemsPage.js';
import championship from './Championship.js'; 
// Style
import './static/css/projects.css';
 
function showProjects(props) {
  return (
    <div className="projects">
      <BrowserRouter>

        {/* Project Championship Management! */}
        <div className="Management">
          <h1>Projects</h1>
          <Thumbnail
          link="/projects/Championship"
          image={Management}
          title="Championship Management"
          category="App"
          />
        </div>

        {/* Project Jems! */}
        <div className="Jems">
          <Thumbnail
          link="/projects/jemsPage"
          image={logo_Jems}
          title="Jems-Calculate tips"
          category="Web app"
          />
        </div>
        <Route path="/dvirtayeb-github-.io/projects/JemsPage" component={jemsPage}/>
        <Route path="/dvirtayeb-github-.io/projects/Championship" component={championship}/>

      </BrowserRouter>
    </div>
  )
}

export default showProjects;