import React, {Component} from 'react';
import {elastic as Menu } from 'react-burger-menu';
import {Link} from 'react-router-dom';

class SideBar extends Component {
  constructor(props){
    super(props);
    this.hide = this.hide.bind(this);
  }
  hide(){
    Menu.element.style.visibility = "hidden";
  }
  render (){

    return(
        <div className="navigation">
          <div className="navigation-sub">
            <li id="nav_item"><Link to="/dvirtayeb-github-.io" className="item">Home </Link></li>
            <Menu >
                <li ><Link to="/dvirtayeb-github-.io/projects" onclick={this.hide} className="bm-item">Projects </Link></li>
                <li ><Link to="/dvirtayeb-github-.io/Courses" className="bm-item">Courses </Link></li>
                <li ><Link to="/dvirtayeb-github-.io/about" className="bm-item">About </Link></li>
            </Menu>
          </div>
        </div>
    )
  }
}

export default SideBar;